import {
  Component,
  OnInit,
  OnChanges,
  ViewChild,
  ElementRef
} from '@angular/core';
import { Store, StoreModule, select } from '@ngrx/store';
import {
  getsongList,
  getPlayer,
  getplayList,
  getcurrentIndex,
  getplayMode,
  getcurrentSong
} from 'src/app/store/selector/player.selector';
import { Song } from 'src/app/services/data.types/common.interface';
import { SetcurrentIndex } from 'src/app/store/actions/player.actions';

@Component({
  selector: 'app-wy-player',
  templateUrl: './wy-player.component.html',
  styleUrls: ['./wy-player.component.less']
})
export class WyPlayerComponent implements OnInit, OnChanges {
  // 是否正在动画
  animating = false;

  percent = 0;
  bufferPercent = 0;

  // 音量
  volume = 60;

  // 定义歌单列表
  public playerSongList;
  // 定义歌手列表
  public playerList;
  // 定义当前索引
  public playercurrentidx;
  // 定义当前播放模式
  public playerMode;
  // 定义当前播放歌曲
  public playercurrentSong;

  // 歌曲当前播放时间
  public currentTime;
  // 歌曲总时长
  public duration;
  // 定义当前播放状态 控制播放图标
  public toggle = false;
  // 定义是否可以播放 --
  public toggleReady = false;
  @ViewChild('audio', { static: true }) private audioEl: ElementRef;
  private audio: HTMLAudioElement;
  // home/index dispatch 事件 wy-player 监听 store
  constructor(private store$: Store<StoreModule>) {
    const store = this.store$.pipe(select(getPlayer));
    store.pipe(select(getsongList)).subscribe(songlist => {
      this.playerSongList = songlist;
    });
    store.pipe(select(getplayList)).subscribe(playList => {
      this.playerList = playList;
    });
    store.pipe(select(getcurrentIndex)).subscribe(currentIndex => {
      this.playercurrentidx = currentIndex;
    });
    store.pipe(select(getplayMode)).subscribe(playMode => {
      this.playerMode = playMode;
    });
    store.pipe(select(getcurrentSong)).subscribe(list => {
      if (list) {
        this.playercurrentSong = list;
        this.duration = list.dt / 1000;
      }
    });
  }
  onPercentChange(per: number) {
    console.log('执行');

    // if (this.currentSong) {
    //   const currentTime = this.duration * (per / 100);
    //   this.audioEl.currentTime = currentTime;
    //   if (this.playerPanel) {
    //     this.playerPanel.seekLyric(currentTime * 1000);
    //   }
    // }
  }
  onVolumeChange() {}
  ngOnInit() {
    this.audio = this.audioEl.nativeElement;
  }

  ngOnChanges() {}
  onCanplay() {
    this.toggleReady = true;
    this.toggle = true;
    this.audio.play();
  }
  onTimeUpdate(e: Event) {
    this.currentTime = (<HTMLAudioElement>e.target).currentTime;
  }
  onEnded() {}
  onError() {}
  //  音乐暂停事件
  paused() {
    // 可能存在当前无正在播放歌曲情况  --- 歌单数据存在---需要将歌单数据进行重新编排
    if (!this.playercurrentSong) {
      if (this.playerList.length) {
        this.store$.dispatch(SetcurrentIndex({ currentIndex: 0 }));
        this.toggleReady = false;
      }
    } else {
      // 如果可以播放说明有歌曲 否则没有歌曲不能进行操作
      if (this.toggleReady) {
        if (this.toggle) {
          this.audio.pause();
          this.toggle = !this.toggle;
        } else {
          this.audio.play();
          this.toggle = !this.toggle;
        }
      }
    }
  }
  // 音乐上一首
  onPrev(idx: number) {
    if (!this.toggleReady) {
      return;
    }
    const index = idx < 0 ? this.playerSongList.length-1 : idx;
    console.log(index);

    this.store$.dispatch(SetcurrentIndex({ currentIndex: index }));
    console.log(this.playercurrentSong);
    console.log(this.playerSongList);
    
    this.toggleReady = false;
  }
  // 音乐下一首
  onNext(idx: number) {
    if (!this.toggleReady) {
      return;
    }
    const index = idx > this.playerSongList.length ? 0 : idx;
    this.store$.dispatch(SetcurrentIndex({ currentIndex: index }));
    this.toggleReady = false;
  }
  get currentpicUrl(): string {
    return this.playercurrentSong
      ? this.playercurrentSong.al.picUrl
      : 'http://s4.music.126.net/style/web2/img/default/default_album.jpg';
  }
  get currentname(): string {
    return this.playercurrentSong ? this.playercurrentSong.name : '还没放歌';
  }
}
