import {
  Component,
  OnInit,
  Input,
  ChangeDetectionStrategy,
  Output,
  EventEmitter
} from '@angular/core';
import { Personalized } from '../../../services/data.types/common.interface';

@Component({
  selector: 'app-single-sheet',
  templateUrl: './single-sheet.component.html',
  styleUrls: ['./single-sheet.component.less'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class SingleSheetComponent implements OnInit {
  @Input() sheet: Personalized;
  @Output() OutPlay = new EventEmitter<number>();
  constructor() {}

  ngOnInit() {}

  playSheet(evt: MouseEvent, id: number) {
    // 点击播放事件整个流程
    // 1:获取歌单详情, 所有要播放的歌曲都在数据的tracks 属性中, 但是tracks属性中无歌曲播放地址
    // 2: 通过歌单详情teacks中的id获取音乐url
    // 3: 处理歌单数据
    evt.stopPropagation();
    this.OutPlay.emit(id);
  }

  get coverImg(): string {
    return this.sheet.picUrl || this.sheet.coverImgUrl;
  }
}
