import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { timer } from 'rxjs';
import { NzMessageService } from 'ng-zorro-antd';
import { User } from 'src/app/services/data.types/common.interface';
import { HomeService } from 'src/app/services/home.service';

@Component({
  selector: 'app-menber-card',
  templateUrl: './menber-card.component.html',
  styleUrls: ['./menber-card.component.less']
})
export class MenberCardComponent implements OnInit {
  point: number;
  tipTitle = '';
  showTip = false;
  @Input() user: User;
  @Output() openModal = new EventEmitter<void>();
  constructor(
    private homeService: HomeService,
    private messageServe: NzMessageService
  ) {}

  ngOnInit() {}

  onSignin() {
    this.homeService.signin().subscribe(
      res => {
        this.alertMessage('success', '签到成功');
        this.tipTitle = '积分+' + res.point;
        this.showTip = true;
        timer(1500).subscribe(() => {
          this.showTip = false;
          this.tipTitle = '';
        });
      },
      error => {
        this.alertMessage('error', error.msg || '签到失败');
      }
    );
  }

  private alertMessage(type: string, msg: string) {
    this.messageServe.create(type, msg);
  }
}
