import { Injectable, Inject } from '@angular/core';
import { ServicesModule, API_CONFIG } from './services.module';
import { HttpClient, HttpParams } from '@angular/common/http';
import { map } from 'rxjs/internal/operators';
import { Observable } from 'rxjs';
import {
  Banner,
  Playtags,
  Personalized,
  Singer,
  SingerParam,
  Signin
} from 'src/app/services/data.types/common.interface';
import queryString from 'query-string';
@Injectable({
  providedIn: ServicesModule
})
export class HomeService {
  constructor(
    private http: HttpClient,
    @Inject(API_CONFIG) private uri: string
  ) {}

  getBanner(): Observable<Banner[]> {
    return this.http
      .get(this.uri + '/banner')
      .pipe(map((res: { banners: Banner[] }) => res.banners));
  }
  // /playlist/hot
  getHots(): Observable<Playtags[]> {
    return this.http.get(this.uri + '/playlist/hot').pipe(
      map((res: { tags: Playtags[] }) => {
        return res.tags
          .sort((x: Playtags, y: Playtags) => {
            return x.position - y.position;
          })
          .slice(0, 5);
      })
    );
  }
  getPersonalized(): Observable<Personalized[]> {
    return this.http
      .get(this.uri + '/personalized')
      .pipe(map((res: { result: Personalized[] }) => res.result.slice(0, 16)));
  }

  getSinger(arg: SingerParam = defaultSingerParam): Observable<Singer[]> {
    const params = new HttpParams({ fromString: queryString.stringify(arg) });
    return this.http
      .get(this.uri + '/artist/list?' + params)
      .pipe(map((res: { artists: Singer[] }) => res.artists));
  }
  // 签到
  signin(): Observable<Signin> {
    const params = new HttpParams({
      fromString: queryString.stringify({ type: 1 })
    });
    return this.http
      .get(this.uri + 'daily_signin', { params })
      .pipe(map(res => res as Signin));
  }
}

const defaultSingerParam: SingerParam = {
  limit: 12,
  cat: 5001,
  offset: 0
};
