import { Component, OnInit, ViewChild } from '@angular/core';
import { HomeService } from 'src/app/services/home.service';
import { NzCarouselComponent } from 'ng-zorro-antd';
import {
  Banner,
  Playtags,
  Personalized,
  Singer
} from 'src/app/services/data.types/common.interface';
import { ActivatedRoute } from '@angular/router';
import { map } from 'rxjs/internal/operators';
import { SingerDetailService } from 'src/app/services/sheet.service';
import { Store } from '@ngrx/store';
import { AppStoreModule } from 'src/app/store';
import {
  SetsongList,
  SetPlaying,
  SetplayList,
  SetcurrentIndex
} from 'src/app/store/actions/player.actions';
@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.less']
})
export class IndexComponent implements OnInit {
  public carouselActiveIndex = 0;
  public banners: Banner[];
  public hotTags: Playtags[];
  public songSheetList: Personalized[];
  public singers: Singer[];
  @ViewChild(NzCarouselComponent, { static: true })
  private nzCarousel: NzCarouselComponent;

  constructor(
    private route: ActivatedRoute,
    private Singerservice: SingerDetailService,
    private store$: Store<AppStoreModule>
  ) {
    this.route.data
      .pipe(map(res => res.key))
      .subscribe(([Banner, Playtags, Personalized, Singer]) => {
        this.banners = Banner;
        this.hotTags = Playtags;
        this.songSheetList = Personalized;
        this.singers = Singer;
      });
  }

  ngOnInit() {}

  onChangeSlide(type: 'pre' | 'next') {
    this.nzCarousel[type]();
  }
  onBeforeChange(data) {
    this.carouselActiveIndex = data.to;
  }
  onPlaySheet(id: number) {
    this.Singerservice.getPlaylist(id).subscribe(res => {
      this.store$.dispatch(SetsongList({ songList: res }));
      this.store$.dispatch(SetplayList({ playList: res }));
      this.store$.dispatch(SetcurrentIndex({ currentIndex: 0 }));
    });
  }
  toInfo(id) {}
}
