import { Resolve } from '@angular/router';
import { HomeService } from 'src/app/services/home.service';
import { forkJoin, Observable } from 'rxjs';
import {
  Banner,
  Playtags,
  Personalized,
  Singer
} from 'src/app/services/data.types/common.interface';
import { take, first } from 'rxjs/internal/operators';
import { Injectable } from '@angular/core';

type HomeType = [Banner[], Playtags[], Personalized[], Singer[]];
@Injectable()

export class HomeResolverService implements Resolve<HomeType> {
  constructor(private homeService: HomeService) {}

  resolve(): Observable<HomeType> {
    return forkJoin([
      this.homeService.getBanner(),
      this.homeService.getHots(),
      this.homeService.getPersonalized(),
      this.homeService.getSinger()
    ]).pipe(take(1));
  }
}
