import { Injectable, Inject } from '@angular/core';
import { Observable } from 'rxjs';
import { SongSheet, Song } from './data.types/common.interface';
import { HttpClient, HttpParams } from '@angular/common/http';
import { API_CONFIG } from './services.module';
import { map, pluck, switchMap } from 'rxjs/internal/operators';
import { SongUrlService } from './song.service';

@Injectable({
  providedIn: 'root'
})
export class SingerDetailService {
  constructor(
    private http: HttpClient,
    private Songurlservice: SongUrlService,
    @Inject(API_CONFIG) private uri: string
  ) {}

  // 获取歌单详情 --SongSheet
  getPlaylistDetail(id: number): Observable<SongSheet> {
    const params = new HttpParams().set('id', id.toString());
    return this.http
      .get(this.uri + '/playlist/detail', { params })
      .pipe(map((res: { playlist: SongSheet }) => res.playlist));
  }

  // 获取所有处理过的歌曲数据集合
  getPlaylist(id: number): Observable<Song[]> {
    return this.getPlaylistDetail(id).pipe(
      pluck('tracks'),
      switchMap(tracks => this.Songurlservice.getSongUrlList(tracks))
    );
  }
}
