import { Injectable, Inject } from '@angular/core';
import { Observable, observable } from 'rxjs';
import { SongSheet, Song, SongUrl } from './data.types/common.interface';
import { HttpClient, HttpParams } from '@angular/common/http';
import { API_CONFIG } from './services.module';
import { map } from 'rxjs/internal/operators';

@Injectable({
  providedIn: 'root'
})
export class SongUrlService {
  constructor(
    private http: HttpClient,
    @Inject(API_CONFIG) private uri: string
  ) {}

  // 获取音乐url--Song
  getSongUrl(ids: string): Observable<SongUrl[]> {
    const params = new HttpParams().set('id', ids.toString());
    return this.http
      .get(this.uri + '/song/url', { params })
      .pipe(map((res: { data: SongUrl[] }) => res.data));
  }

  // 获取音乐url集合--Song 接收参数可为单个歌曲/歌曲集合
  getSongUrlList(songs: Song | Song[]): Observable<Song[]> {
    // 首先判断song 是否为数组
    const songArr = Array.isArray(songs) ? songs.slice() : [songs];
    const ids = songArr.map(item => item.id).join(',');
    return this.getSongUrl(ids).pipe(
      map(urls => this.generSongUrlList(songArr, urls))
    );
    // return Observable.create(observable => {
    //   observable.next(
    //     this.getSongUrl(ids).subscribe(urls => {
    //       this.generSongUrlList(songArr, urls);
    //     })
    //   );
    // });
  }
  // 需要把 urli数据中的url 拼接到 song 中
  private generSongUrlList(songs: Song[], urls: SongUrl[]): Song[] {
    const result = [];
    songs.forEach(song => {
      const url = urls.find(songUrl => songUrl.id === song.id).url;
      if (url) {
        song.url = url;
        result.push(song);
        // result.push({ ...song, url });
      }
    });
    return result;
  }
}
